import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
import { MarketProvider } from '../../providers/market/market';
import { ToolsProvider } from '../../providers/tools/tools';
import { LoginProvider } from '../../providers/login/login';

@IonicPage()
@Component({
  selector: 'page-keterangan',
  templateUrl: 'keterangan.html',
})
export class KeteranganPage {
  private disclaimer: any[];
  private clientId:any;
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams, 
    public mkt: MarketProvider,
    public lgn: LoginProvider,
    public tools:ToolsProvider,
    public platform: Platform
  ) {
    this.getDisclaim();
  }

  ionViewDidLoad(){
    this.platform.ready().then(()=>{
      this.checkLogin();
    });
  }

  checkLogin(){
    this.tools.getStg('login').then(
      (data)=>{
        // console.log('check login',data);
        if(!data || data.level !== '0'){
          this.navCtrl.setRoot('LoginPage');
        }else{
          this.clientId = data.clientId;
          this.tools.getStg('ip').then(
            (rslt)=>{
              // console.log('ip',rslt);
              if(rslt){
                this.lgn.logger(this.clientId,'Masuk halaman Syarat & Ketentuan', rslt.ip).subscribe();
                this.tools.getStg('dscl').then(
                  res=>{
                    // console.log('stg',res);
                    if(res){
                      this.disclaimer = res;
                    }else{
                      this.getDisclaim();
                    }
                  },err=>{
                    this.getDisclaim();
                  }
                );
              }
            }
          );
        }
      },
      err=>{
        console.log('History-> getStgLogin', err);
      }
    );
  }

  getDisclaim(){
    this.mkt.getDisclalimWeb().subscribe(
      data=>{
        // console.log('req',data);
        this.tools.setStg('dscl',data);
        this.disclaimer = data;
      }
    );
  }
}



